import React,{Component} from 'react'
import { IndexLink, Link } from 'react-router'
import './Header.scss'
import BaiduLogo from '../assets/baiduLogo.png'
import {BaseButton} from '../lib/BaseComponent'
import Constant from '../lib/Constant'
import IsPcOrNot from '../lib/IsPcOrNot'
import menu_icon from '../lib/menu.png'
import HeaderBanner from '../lib/header_ bg.png'
import {browserHistory} from 'react-router'
import en from '../../locale/en';
import zh from '../../locale/zh';

let messages = {};

messages['en'] = en;
messages['zh-CN'] = zh;
messages['zh-cn'] = zh;

class FormattedMessage extends Component {
    render() {
        return <span>{messages[localStorage['language']][this.props.id]}</span>
    }
}
//移动端logo
class WapLogo extends Component {

    componentDidMount() {

    }
    go = () => {
        browserHistory.push('/');
    };
    render() {
        return <img onClick={this.go} style={{cursor:"pointer",height:"20px",lineHeight:"40px",verticalAlign:"middle",margin:"10px 20px"}} src={BaiduLogo}/>
    }
}
//pc端logo
class Logo extends Component {
    componentDidMount() {

    }
    go = () => {
        browserHistory.push('/');
    };
    render() {
        return <img onClick={this.go} style={{cursor:"pointer",height:"30px",lineHeight:"60px",verticalAlign:"middle",margin:"15px 0"}} src={BaiduLogo}/>
    }
}

class Collapsed extends Component {
    constructor(props) {
        super(props);
    }
    handleClick = () => {
        this.props.handleClick();
    };
    render() {
        return <button onClick={this.handleClick} style={{
            float:"right",
            width:"30px",
            height:"30px",
            backgroundImage:"url("+menu_icon+")",
            backgroundPosition:"center",
            backgroundSize:"contain",
            margin:"5px 20px",
            border:"0",
            backgroundColor:"transparent"
        }}>
        </button>
    }
}
class Header extends Component {
    constructor(props) {
        super(props);
        this.popover = {
            open : false
        };
        this.user = {
            name : '',
            logined : false
        };
        this.login_flag = false;
    }

    ready = (link,imgUrl,title,desc) => {
        var share = (link,imgUrl,title,desc) => {
            let ShareJson = {
                link : link,
                imgUrl :imgUrl,
                title : title,
                desc : desc
            };
            wx.onMenuShareTimeline(ShareJson);
            wx.onMenuShareAppMessage(ShareJson);
        };
        var refresh_token = (success) => {
            $.get("/youcaihua/portal/update_access_token/",()=>{success()});
        };
        $.post("/youcaihua/portal/wxconfig/",{
            "url" : location.href
        },(data)=>{
            wx.config(data);
            wx.ready(()=>{
                share(link,imgUrl,title,desc);
            });
            wx.error((res)=>{
                refresh_token(share(link,imgUrl,title,desc));
            });
        })
    };
    componentDidMount() {
        this.ready('https://create.baidu.com/','https://create.baidu.com/wxlogo.jpg','2019百度AI开发者大会','2019百度AI开发者大会邀请您来参加');
        $.get(Constant.url + 'hasLoginBaidu.do',(data)=>{
            if(data.error_no == '0') {
                this.user.name = data.data.displayName;
                this.user.logined = true;
                this.setState(this.user);
                // console.log(this.user);
            }
        })
    }
    handleClick = () => {
        this.popover.open = !this.popover.open;
        this.setState(this.popover);
    }

    login = () => {
        if(IsPcOrNot.get()) {
            window.instance.show();
        }
        else{
            if(!this.login_flag) {

                $('#login-wrap').css("display", "block");
                $('#login').css("display", "block");
                this.popover.open = false;
                this.setState(this.popover);
                this.login_flag = true;
            }
        }
    };
    changeLanguage = () => {
        if(localStorage['language'] == 'en')  {
            localStorage['language'] = 'zh-cn';
        }
        else {
            localStorage['language'] = 'en';
        }
        location.href = location.href;
    };

    render() {
        return IsPcOrNot.get()?<div style={{backgroundColor:"#000",height:"60px"}}>
            <div style={{width:"1200px",margin:"0 auto",height:"100%"}}>
                <Logo/>
                <div style={{display:"inline-block",margin:"0 0 0 140px"}}>
                    <IndexLink className="nav-link" to={Constant.router_preffix+'/'} activeClassName='route--active'>
                        <FormattedMessage id="index_page"/>
                    </IndexLink>
                    <Link className="nav-link" to={Constant.router_preffix+'/ticket_type'} activeClassName='route--active'>
                        <FormattedMessage id="join_meeting_buy_tickets"/>
                    </Link>
                    <Link className="nav-link" to={Constant.router_preffix+'/agenda'} activeClassName='route--active'>
                        <FormattedMessage id="meeting_program"/>
                    </Link>
                    <Link className="nav-link" to={Constant.router_preffix+'/speakers'} activeClassName='route--active'>
                        <FormattedMessage id="meeting_guest"/>
                    </Link>
                    <Link className="nav-link" to={Constant.router_preffix+'/travel'} activeClassName='route--active'>
                        <FormattedMessage id="map_road"/>
                    </Link>
                    {/*<Link className="nav-link" href="http://astar2018.baidu.com" target="_blank" activeClassName='route--active'>*/}
                        {/*<FormattedMessage id="astar"/>*/}
                    {/*</Link>*/}
                    <Link className="nav-link" href="https://ai.baidu.com"  target="_blank" activeClassName='route--active'>
                        <FormattedMessage id="ai_baidu_com"/>
                    </Link>
                </div>
                <span onClick={this.changeLanguage} style={{float:"right",cursor:"pointer",lineHeight:"60px",color:"#fff"}}>
                    {
                        localStorage['language'] == 'en'? "中文":"English"
                    }
                </span>
                {
                    this.user.logined ?
                        <Link className="nav-link" style={{float:"right",lineHeight:"60px"}} to={Constant.router_preffix+'/my_tickets'} activeClassName='route--active'>
                            {this.user.name + ' (' + messages[localStorage['language']]['my_ticket'] + ')'}
                        </Link> :
                        <BaseButton action={this.login.bind(this)} style={{float: "right", margin: "14px 20px"}}
                                    text="login"/>
                }

            </div>
        </div>:<div id="wap-nav">
            <div style={{
                width:'100%',
                height:'40px',
                background:'#000',
                overflow:"hidden",
                position:"fixed",
                top:0,
                left:0,
                zIndex:'9999'
            }}>
                <WapLogo/>
                <Collapsed handleClick={this.handleClick.bind(this)}/>
            </div>
            <div style={{
                width:"100%",
                position:"fixed",
                top:'40px',
                left:0,
                right:'0',
                bottom:0,
                backgroundColor:"rgba(0,0,0,0.5)",
                overflow:'hidden',
                zIndex:'999',
                display:this.popover.open?"block":"none"
            }}>
            </div>
            <div style={{
                width:'100%',
                height:this.popover.open?'auto':'0',
                position:"fixed",
                top:'40px',
                left:'0',
                right:'0',
                zIndex:'999',
                overflow:"hidden",
                // transition:'.1s ease'
            }} id="pop-menu">
                <div style={{
                    backgroundImage:"url("+HeaderBanner+")",
                    backgroundSize:"cover",
                    backgroundRepeat:"no-repeat",
                    backgroundPosition:"top",
                    width:'100%',
                    height:'2.4rem',
                    position:"relative"
                }}>
                    {
                        this.user.logined?
                            <div style={{
                                color:"#444",
                                right:"20px",
                                top:"50%",
                                marginTop:"-20px",
                                position:"absolute",
                            }}>
                                {this.user.name}
                            </div>:
                            <button className="base_type_btn" onClick={this.login.bind(this)} style={{
                                color:"#fff",
                                right:"20px",
                                top:"50%",
                                marginTop:"-20px",
                                position:"absolute",
                                borderRadius:"3px",
                                padding:"8px 25px"
                            }}>
                                <FormattedMessage id="login"/>
                            </button>
                    }

                </div>

                <div className="menu-item">
                    <IndexLink to={Constant.router_preffix+'/'} activeClassName='route--active'>
                        <div className="a-link">
                            <FormattedMessage id="index_page"/>
                        </div>
                    </IndexLink>
                </div>
                <div className="menu-item">
                    <Link to={Constant.router_preffix+'/ticket_type'} activeClassName='route--active'>
                        <div className="a-link">
                            <FormattedMessage id="join_meeting_buy_tickets"/>
                        </div>
                    </Link>
                </div>
                <div className="menu-item">
                    <Link to={Constant.router_preffix+'/agenda'} activeClassName='route--active'>
                        <div className="a-link">
                            <FormattedMessage id="meeting_program"/>
                        </div>
                    </Link>
                </div>
                <div className="menu-item">
                    <Link to={Constant.router_preffix+'/speakers'} activeClassName='route--active'>
                        <div className="a-link">
                            <FormattedMessage id="meeting_guest"/>
                        </div>
                    </Link>
                </div>
                <div className="menu-item">
                    <Link to={Constant.router_preffix+'/travel'} activeClassName='route--active'>
                        <div className="a-link">
                            <FormattedMessage id="map_road"/>
                        </div>
                    </Link>
                </div>
                <div className="menu-item">
                    <Link href="https://ai.baidu.com" target="_blank" activeClassName='route--active'>
                        <div className="a-link">
                            <FormattedMessage id="ai_baidu_com"/>
                        </div>
                    </Link>
                </div>
                {
                    this.user.logined?
                        <div className="menu-item">
                            {
                                this.user.logined?
                                    <Link to={Constant.router_preffix+'/my_tickets'} activeClassName='route--active'>
                                        <div className="a-link">
                                            <FormattedMessage id="my_ticket"/>
                                        </div>
                                    </Link>:null

                            }
                        </div>:null
                }
                <div className="menu-footer" style={{textAlign:"right"}}>
                        <span onClick={this.changeLanguage} style={{padding:"10px",cursor:"pointer",lineHeight:"34px"}}>
                        {
                            localStorage['language'] == 'en'? "中文":"English"
                        }
                        </span>
                </div>
            </div>

        </div>
    }

}

export default Header



