// We only need to import the modules necessary for initial render
import CoreLayout from '../layouts/CoreLayout'
import Constant from '../components/lib/Constant'

/*  Note: Instead of using JSX, we recommend using react-router
 PlainRoute objects to build route definitions.   */

export const createRoutes = (store) => ({
    path: '/',
    component: CoreLayout,
    getIndexRoute (location, cb) {
        cb(null,[
            require('./IndexPage').default(store)
        ])
    },
    getChildRoutes (location, cb) {
        require.ensure([], (require) => {
            cb(null, [
                // Remove imports!
                require('./StudentApply').default(store),
                require('./GetTicket').default(store),
                require('./MyTickets').default(store),
                require('./PaySuccess').default(store),
                require('./Pay').default(store),
                require('./TicketTypes').default(store),
                require('./Guests').default(store),
                require('./Map').default(store),
                require('./Order').default(store),
                require('./Program').default(store),
                require('./Qrcode').default(store),
                require('./News').default(store),
                require('./Invoice').default(store),
                require('./Specialinvoice').default(store),
                require('./invoiceDetail').default(store),
                require('./terminal').default(store),
                require('./invitation').default(store)
            ])
        })
    }
})

/*  Note: childRoutes can be chunked or otherwise loaded programmatically
 using getChildRoutes with the following signature:

 getChildRoutes (location, cb) {
 require.ensure([], (require) => {
 cb(null, [
 // Remove imports!
 require('./Counter').default(store)
 ])
 })
 }

 However, this is not necessary for code-splitting! It simply provides
 an API for async route definitions. Your code splitting should occur
 inside the route `getComponent` function, since it is only invoked
 when the route exists and matches.
 */

export default createRoutes



