import React from 'react'
import Header from '../../components/Header'
import './CoreLayout.scss'
import '../../styles/core.scss'
import BaiduLogin from '../../components/lib/BaiduLogin'
import IsPcOrNot from "../../components/lib/IsPcOrNot";

export const CoreLayout = ({ children }) => (
  <div style={{height:"100%",backgroundColor:"#ffffff",position:"relative",paddingTop:IsPcOrNot.get()?'':'40px'}}>
    <BaiduLogin/>
    <div className="app-container" style={{position:"relative",height:"100%"}}>
      {children}
    </div>
  </div>
)

CoreLayout.propTypes = {
  children: React.PropTypes.element.isRequired
}

export default CoreLayout



