import React,{Component, PropTypes} from 'react'
import IsPcOrNot from './IsPcOrNot.js'
import logo from '../../components/assets/login_logo.png'
import close from '../../components/assets/login_off.png'
import $ from 'n-zepto'
let script_load_flag = false;
class BaiduLogin extends Component {
    componentDidMount() {
        if(!script_load_flag) {
            script_load_flag = true;
            $.getScript = function (url, success, error) {
                let script = document.createElement("script"),
                    $script = $(script);
                script.src = url;

                $("head").append(script);
                $script.bind("load", success);
                $script.bind("error", error);
            };
            var isHttps;
            if(document.location.protocol == 'http:'){
                isHttps = false
            }
            if(document.location.protocol == 'https:'){
                isHttps = true
            }
            var href;
            if(IsPcOrNot.get()){
                if(isHttps){
                    href = 'https://passport.baidu.com/passApi/js/uni_login_wrapper.js?cdnversion='
                }else{
                    href = 'http://passport.baidu.com/passApi/js/uni_login_wrapper.js?cdnversion='
                }
            }else{
                if(isHttps){
                  href = 'https://wappass.baidu.com/static/touch/js/api/wrapper.js?cdnversion='
                }else{
                    href = 'http://wappass.baidu.com/static/touch/js/api/wrapper.js?cdnversion='
                }
            }
            $.getScript(href + new Date().getTime(),
                function () {
                    let url =isHttps?"https://create.baidu.com":'http://create.baidu.com';
                    if(IsPcOrNot.get()) {
                        window.instance = passport.pop.init({
                            apiOpt: {
                                staticPage: url + '/v3Jump.html',
                                product: 'campus',
                                u: location.href,
                                memberPass: true,
                                safeFlag: 0,
                                overseas:'1'

                            },
                            cache: true,
                            forgetLink:isHttps?'https://passport.baidu.com/?getpass_index':'http://passport.baidu.com/?getpass_index',
                            registerLink:isHttps?'https://passport.baidu.com/v2/?reg&tpl=campus&overseas=1&u=':'http://passport.baidu.com/v2/?reg&tpl=campus&overseas=1&u=' + encodeURI(location.href),
                            tangram: true
                        });
                    }
                    else {
                        passport.use("smsLogin",{
                            "library":"true",
                            "defaultCss":"true",
                            "formContainer":$("#login")
                        },(apiMagic)=>{
                            let options = {
                                staticPage: url + '/v3Jump.html',
                                product: 'campus',
                                u: location.href,
                                overseas:'1'
                            };
                            window.instance = new apiMagic.passport.smsLogin(options);
                            window.instance.render($("#login")[0]);
                        })
                    }
                });
        }

    }
    off = () => {
        // $("#login-wrap").css("display","none");
      window.location.href = window.location.href;
    }
    render() {
        return <div id="login-wrap" style={{padding:'0 10px'}}>
            <div id="loginLogo" style={{
                backgroundImage:"url("+logo+")",
                backgroundSize:"cover",
                height:"0.73rem",
                width:"2.27rem",
                margin:"0.1rem auto 0 auto"
            }}></div>
            <div id="off" onClick={this.off} style={{
            backgroundImage:"url("+close+")",
            backgroundSize:"contain",
            width:"16px",
            height:"16px",
            backgroundRepeat:"no-repeat",
            position:"absolute",
            right:"10px",
            top:"10px"
            }}/>
        <div id="login"></div>
        </div>
    }

}
export default BaiduLogin;


